
import { Component, OnInit, Input, Output, EventEmitter, OnChanges, SimpleChanges } from '@angular/core';
import { DynamicField } from '@acaprojects/ngx-widgets';
import { Validators } from '@angular/forms';

import { AppService } from '../../../services/app.service';
import { BaseComponent } from '../../globals/base.component';
import { FormFormatters } from './form.formatters';
import { FormValidators } from './form.validators';

import { CustomDateFieldComponent } from '../custom-fields/date-field/date-field.component';
import { CustomTimeFieldComponent } from '../custom-fields/time-field/time-field.component';
import { CustomDurationFieldComponent } from '../custom-fields/duration-field/duration-field.component';
import { CustomUserListFieldComponent } from '../custom-fields/user-list-field/user-list-field.component';
import { CustomWeekFieldComponent } from '../custom-fields/week-field/week-field.component';
import { CustomUserSearchFieldComponent } from '../custom-fields/user-search-field/user-search-field.component';
import { CustomTermsFieldComponent } from '../custom-fields/terms-field/terms-field.component';
import { CustomPreferenceFieldComponent } from '../custom-fields/preference-field/preference-field.component';
import { CustomCostFieldComponent } from '../custom-fields/cost-field/cost-field.component';
import { CustomRecurrenceFieldComponent } from '../custom-fields/recurrence-field/recurrence-field.component';
import { CustomGuestSettingsFieldComponent } from '../custom-fields/guest-settings-field/guest-settings-field.component';

import * as moment from 'moment';
import { CustomEditorFieldComponent } from '../custom-fields/editor-field/editor-field.component';
import { rulesForSpace } from '../../utilities/booking.utilities';
import { IUser } from '../../../services/data/users.service';

export interface IBookingFormSettings {
    edit?: boolean;
    week?: boolean;
    recurrence?: boolean;
    attendees?: number;
    fixed?: string[];
    hide_errors?: boolean;
}

@Component({
    selector: 'booking-form',
    templateUrl: './booking-form.template.html',
    styleUrls: ['./booking-form.styles.scss']
})
export class BookingFormComponent extends BaseComponent implements OnInit, OnChanges {
    @Input() public form: any = {};
    @Input() public new: boolean;
    @Input() public check: boolean;
    @Input() public edit: boolean;
    @Input() public valid: boolean;
    @Input() public settings: IBookingFormSettings = {};
    @Input() public error: any = {};
    @Output() public errorChange = new EventEmitter();
    @Output() public formChange = new EventEmitter();
    @Output() public validChange = new EventEmitter();
    @Output() public event = new EventEmitter();

    public model: { [name: string]: any } = { form: {}, error: {}, display: {} };

    private last_change: number;

    constructor(private service: AppService) {
        super();
    }

    public ngOnInit() {
        if (!this.form) { this.form = {}; }
        this.model.initialised = 0;
        if (!this.model.formatters) { this.model.formatters = {}; }
        if (!this.model.validators) { this.model.validators = {}; }
        if (!this.model.actions) { this.model.actions = {}; }
        if (!this.model.custom_elements) { this.model.custom_elements = {}; }
        this.init();
    }

    public ngOnChanges(changes: SimpleChanges): void {
        if (changes.form && (this.model.initialised < 1 || this.form.changed !== this.last_change)) {
            this.model.initialised++;
            this.last_change = this.form.changed || 0;
            this.timeout('form_change', () => this.processFields(), 50);
        }
        if (changes.new) {
            this.model.initialised = 0
            this.timeout('form_change', () => this.processFields(), 50);
        }
        if (changes.check && this.model.initialised) {
            this.timeout('valid', () => this.processFields(true), 50);
        }
    }

    public init() {
        if (!this.service.ready()) {
            return this.timeout('init', () => this.init());
        }
        this.model.user = this.service.Users.current();
        this.model.settings = this.service.Settings.get('app.booking');
        this.model.actions = {
            room: () => this.setRoom(),
            attendees: () => this.event.emit('Attendees'),
            // time: () => this.setPeriod(),
            // date: () => this.setDate(),
            // availability: () => this.selectAvailability(),
            catering: () => this.event.emit('catering'),
            recurrence: () => this.setRecurrence(),
            conferencing: (v?) => this.setConferencing(v)
        };
        this.model.validators = {
            date: [FormValidators.date],
            duration: [FormValidators.duration()],
            attendees: [FormValidators.attendees(this.model.user, this.model.settings.min_attendees)],
            terms: [Validators.requiredTrue]
        };
        this.model.formatters = {
            attendees: FormFormatters.attendees(this.model.user),
            date: FormFormatters.date,
            start: FormFormatters.timestring,
            room: FormFormatters.space,
            recurrence: FormFormatters.recurrence,
            host_index: (index: number) => this.getHostOptions()[index].name
        };
        this.model.options = {
            host_index: this.getHostOptions()
        };
        this.model.custom_elements = {
            date: CustomDateFieldComponent,
            host: CustomUserSearchFieldComponent,
            start: CustomTimeFieldComponent,
            duration: CustomDurationFieldComponent,
            attendees: CustomUserListFieldComponent,
            weekday: CustomWeekFieldComponent,
            terms: CustomTermsFieldComponent,
            preferences: CustomPreferenceFieldComponent,
            extras: CustomPreferenceFieldComponent,
            cost: CustomCostFieldComponent,
            recurrence: CustomRecurrenceFieldComponent,
            description: CustomEditorFieldComponent,
            notify_user: CustomGuestSettingsFieldComponent
        };
        this.model.defaults = {
            date: () => moment().valueOf(),
            host_index: 0,
            booking_type: 0,
            time: () => {
                const date = moment();
                if (date.minutes() % 5 === 0) { date.minutes(date.minutes() + 1); }
                return date.minutes(Math.ceil(date.minutes() / 5) * 5);
            }
        };
        this.model.initialised = 1;
        this.timeout('form_change', () => this.processFields(), 100);
    }

    public processFields(dirty: boolean = false) {
        const settings = this.service.Settings.get('app') || {};
        let fields = JSON.parse(JSON.stringify(settings.booking.fields || []));
        // Even if there is not room, a field is provided in booking form to click through the room
        // We need to add details of clicking through to room-search component in dynamics fields when this field is being clicked on
        if (this.edit || this.form.room) {
            fields.unshift({ key: 'room', label: 'Select space', control_type: 'select' });
        }
        const options = this.getHostOptions();
        this.model.options.host_index = options;
        if (this.form.host) {
            this.form.host_index = options.findIndex(i => i.email === this.form.host.email);
        }
        this.model.formatters.conferencing = FormFormatters.conference(this.form.conference);
        this.model.validators.start = [FormValidators.startTime(this.form.date)];
        let time_readonly = false;
        const process_item = (f: DynamicField<any>) => {
            f.action = this.model.actions[f.key];
            f.format = this.model.formatters[f.key];
            f.validators = this.model.validators[f.key];
            f.options = this.model.options[f.key] || f.options;
            if (f.control_type === 'custom') {
                f.cmp = this.model.custom_elements[f.key];
            }
            f.dirty = dirty;
            f.hide = f.hide || ((f as any).no_edit === true && this.edit) || ((f as any).edit_only === true && !this.edit);
            if (f.children) { f.children.forEach(process_item); }
            if (this.model.defaults[f.key] !== undefined && !f.value) {
                const value = this.model.defaults[f.key];
                f.value = value instanceof Function ? value() : value;
            }
            this.setValue(f);
            if (this.edit && f.key === 'date' && moment().isAfter(moment(f.value), 'm')) {
                f.readonly = true;
                time_readonly = true;
            }
            if (this.edit && (f.key === 'start' || f.key === 'host_index') && time_readonly) {
                f.readonly = true;
                f.validators = [];
            }
            return f;
        };
        fields = fields.map(process_item);
        if (this.form.simple && settings.map.simple) { fields = fields.filter(i => i.simple !== false); }
        this.model.fields = fields;
    }

    public setValue(field) {
        field.dirty = !!this.form[field.key] || !!field.value || field.dirty;
        switch (field.key) {
            case 'host':
                field.value = this.form[field.key] || this.service.Users.current();
                field.dirty = !!this.form[field.key];
                break;
            case 'booking_type':
                const value = this.form[field.key] || field.value;
                field.value = typeof value === 'number' ? value : field.options.findIndex(i => i.id === value);
                break;
            default:
                field.value = this.form[field.key] || field.value;
                break;
        }
    }

    public setRoom() {
        this.service.Overlay.openModal('select-room', {
            data: {
                spaces: this.form.room ? (this.form.room instanceof Array ? this.form.room : [this.form.room]) : [],
                date: this.form.date,
                duration: this.form.duration,
                host: this.form.host,
                // date: this.model.form.date,
                // period: recurr ? recurr.period : null,
                // end: recurr ? recurr.end : null,
                multi: true
            }
        }, (event) => {

            if (event.type === 'Select') {
                this.form.room = event.data.select_list || event.data.selected;
                const list = this.form.room instanceof Array ? this.form.room : [this.form.room];
                list.forEach(rm => {
                    const item = this.service.Rooms.item(rm.id) || rm;
                    const bld: any = this.service.Buildings.list().find(i => i.id === item.level.bld_id) || {};
                    const rules = rulesForSpace({
                        user: this.service.Users.current(),
                        space: item,
                        time: this.form.date,
                        recurr_end: this.form.recurrence ? this.form.recurrence.end : 0,
                        duration: this.form.duration || 60,
                        rules: bld.booking_rules
                    });
                    // Check booking rules
                    if (rules) {
                        item.book_type = rules.auto_approve ? 'Book' : 'Request';
                    }
                });
                this.processFields();
            }
            event.close();
        });
    }

    public setRecurrence() {
        const recurr = this.form.recurrence;
        this.service.Overlay.openModal('recurrence-form', {
            data: {
                date: this.model.form.date,
                period: recurr ? recurr.period : null,
                end: recurr ? recurr.end : null,
                days: recurr ? recurr.days: null
            }
        }, (event) => {
            if (event.type === 'Accept') {
                const { recurr_period, recurr_end, days } = event.data.form;
                this.form.recurrence = {
                    period: recurr_period,
                    end: recurr_end,
                    days
                };
                if (!recurr_period || recurr_end) {
                    this.processFields();
                    this.post(this.form);
                    event.close();
                }
            } else {
                event.close();
            }
        });
    }

    public setConferencing(active?: boolean) {
        return new Promise((resolve) => {
            if (active) {
                this.service.Overlay.openModal('conferencing', { data: { conference: this.form.conference } }, (event) => {
                    if (event.type === 'Confirm' || event.type === 'close') {
                        this.form.conference = event.data.conference;
                        this.form.conferencing = true;
                        this.processFields();
                        this.post(this.form);
                        resolve(true);
                    } else {
                        resolve(false);
                    }
                    event.close();
                });
            } else {
                resolve(false);
            }
        });
    }

    public to(name: string) {
        if (!name) { return Promise.reject(); }
        this.service.navigate(['book', name]);
        return Promise.reject();
    }

    public post(form) {
        if (typeof form.booking_type === 'number') {
            const value = this.model.fields.find(i => i.key === 'booking_type').options[form.booking_type] || {};
            form.booking_type = value.id || value;
        }
        const user = this.service.Users.current();
        form.host = form.host_index ? this.getHostOptions()[form.host_index] : user;
        const attendee_field = this.model.fields.find(i => i.key === 'attendees');
        const length = form.attendees.length;
        if (attendee_field) {
            if (form.host) {
                form.attendees = form.attendees.filter(i => i.email !== form.host.email);
                if (form.attendees.length < length) {
                    this.form.attendees = form.attendees;
                    this.form.host = form.host;
                    this.processFields();
                }
            }
            const valid = [FormValidators.attendees(this.form.host || this.model.user, this.model.settings.min_attendees)];
            const format = [FormFormatters.attendees(this.form.host || this.model.user)];
            this.model.validators.attendees = attendee_field.validators = valid;
            this.model.formatters.attendees = attendee_field.formatters = format;
        }
        form.conference = this.form.conference;
        this.formChange.emit(form);
    }

    /**
     * Get list of users available to select as the meeting host
     */
    private getHostOptions(): IUser[] {
        const user = this.service.Users.current();
        const list: IUser[] = [user];
        if (user.delegates) {
            for (const email of user.delegates) {
                const delegate = this.service.Users.item(email);
                if (delegate) {
                    list.push(delegate);
                } else {
                    this.timeout(`load_user|${email}`, () => {
                        this.service.Users.show(email, { update: true }).then(() => {
                            this.timeout('reload_host_options', () => this.processFields());
                        });
                    });
                }
            }
        }
        return list;
    }
}
