
import * as moment from 'moment';

import { IRoom } from '../../../services/data/rooms.service';
import { IUser } from '../../../services/data/users.service';
import { Utils } from '../../utility.class';

export class FormFormatters {
    public static space(list: IRoom | IRoom[]) {
        if (!list || (list instanceof Array && list.length === 0)) {
            return 'No selected space';
        }
        if (list instanceof Array) {
            return list.length === 1 ? list[0].name : `${list.length} Space${list.length !== 1 ? 's' : ''}`;
        } else {
            const capacity = list.capacity ? '(' + list.capacity + ' ' + (list.capacity === 1 ? 'person' : 'people') + ')' : '';
            return `${list.name} ${capacity}`;
        }
    }

    public static attendees(user: IUser) {
        return (list: IUser[]) => {
            let attendee_str: string;
            if (list && list.length > 0) {
                const users = [...list];
                const result = users.find((a) => a.email === user.email);
                if (result) {
                    users.splice(users.indexOf(result), 1);
                }
                attendee_str = `${users.length + 1} Attendee${users.length + 1 === 1 ? '' : 's'}; ${user.name}`;
                for (const item of users) {
                    if (attendee_str) { attendee_str += ', '; }
                    attendee_str += item.name;
                }
            }
            return attendee_str;
        };
    }

    public static date(timestamp: number) {
        return moment(timestamp).format('Do MMMM YYYY');
    }

    public static time(timestamp: number) {
        return moment(timestamp).format('h:mm A');
    }

    public static period(duration: number) {
        return (timestamp: string) => {
            const parts = timestamp.split(':');
            const date = moment().hours(+parts[0]).minutes(+parts[1]);
            return `${date.format('h:mm A')} - ${date.add(duration, 'm').format('h:mm A')} (${Utils.humaniseDuration(duration)})`;
        };
    }

    public static duration(duration: number) {
        return Utils.humaniseDuration(duration);
    }

    public static recurrence(value: { period: string | number, end: number }) {
        if (!value || !value.period) { return 'No recurrence'; }
        const period = typeof value.period === 'string' ? value.period : ['None', 'Daily', 'Weekly', 'Biweekly', 'Monthly'][value.period];
        const end = value.end ? `until ${moment(value.end).format('DD MMM YYYY')}` : 'forever';
        return `${period} ${end}`;
    }

    public static catering(list: any[]) {
        return 'No catering';
    }

    /**
     * Format 24h time string as a 12h time string. `HH:mm` to `h:mm A`
     * @param time Time to format. `HH:mm`
     */
    public static timestring(time: string) {
        if (!time) { return '12:00 AM'; }
        const parts = time.split(':');
        const date = moment().hours(+parts[0]).minutes(+parts[1]);
        return date.format('h:mm A');
    }

    public static conference(data: any) {
        return (active: boolean) => {
            if (active && data) {
                let output = '';
                if (data.zoom) { output += 'Zoom added'; }
                if (data.mac_din) {
                    if (output) { output += ', '; }
                    output += 'Audio conferencing(MAC/DIN) added';
                    if (data.list && data.list.length) {
                        output += ` for ${data.list.length} countr${data.list.length === 1 ? 'y' : 'ies'}`;
                    }
                }
                return output;
            }
            return '';
        };
    }
}
