
import { trigger, state, style, animate, transition } from '@angular/animations';
import { OverlayContentComponent } from '@acaprojects/ngx-widgets';
import { Component } from '@angular/core';

import { Utils } from '../../shared/utility.class';
import { AppService } from '../../services/app.service';

@Component({
    selector: 'guest-settings-modal',
    templateUrl: './guest-settings-modal.component.html',
    styleUrls: ['./guest-settings-modal.component.scss'],
    animations: [
        trigger('show', [
            transition(':enter', [style({ opacity: 0, height: 0 }), animate(300, style({ opacity: 1, height: '*' }))]),
            transition(':leave', [style({ opacity: 1, height: '*' }), animate(300, style({ opacity: 0, height: 0 }))]),
        ])
    ]
})
export class GuestSettingsModalComponent extends OverlayContentComponent<AppService> {
    public model: any = {};

    public init() {
        const bld = this.service.Buildings.current();
        const concierge = {
            name: `${bld.name} Concierge`,
            email: bld.reception_email || 'ln-reception@mckinsey.com'
        };
        const user_email = this.model.user ? this.model.user.email : '';
        const host = this.model.host;
        if (!this.model.guest_settings) { this.model.guest_settings = {}; }
        this.model.greeter_options = [
            { id: 'host', label: `Host of the meeting (${host.name})`, user: host },
            { id: 'concierge', label: 'Concierge at front desk', user: concierge },
            { id: 'other', label: 'Someone else' }
        ];
        const id = user_email ?
                (user_email === host.email ?
                    'host' :
                    (user_email === concierge.email ?
                        'concierge' :
                        'other')) :
                'host';
        if (id === 'other') { this.model.greeter_options[2].user = { ...this.model.user }; }
        this.updateGreeter(id);
    }

    public updateGreeter(id: string) {
        this.model.greeter = this.model.greeter_options.find(el => el.id === id);
    };

    public search(filter: string) {
        this.timeout('search', () => {
            if (filter && filter.length >= 3) {
                if (!this.model.result) {
                    this.service.Users.query({ q: filter }).then(users => {
                        this.model.result = users;
                        this.filterResult(filter);
                        this.model.show = true;
                    })
                } else {
                    this.filterResult(filter);
                    this.model.show = true;
                }
            } else {
                this.model.result = null;
            }
        });
    }

    public filterResult(filter: string) {
        const group = [...this.model.result, this.service.Contacts.list()];
        this.model.filtered = Utils.filter(filter, group, ['name', 'email']);
        this.model.filtered.forEach(i => {
            if (i.match_name) {
                i.match_name = i.match_name.replace(/\`[a-zA-Z0-9\@\.\_]*\`/g, '<span class="highlight">$&</span>');
                i.match_name = i.match_name.replace(/\`/g, '');
            }
            if (i.match_email) {
                i.match_email = i.match_email.replace(/\`[a-zA-Z0-9\@\.\_]*\`/g, '<span class="highlight">$&</span>');
                i.match_email = i.match_email.replace(/\`/g, '');
            }
        });
    }

    public setGreeter(item) {
        this.model.greeter.user = item;
        this.model.search = null;
        this.model.result = null;
    }

    public removeGreeter() {
        this.model.greeter.user = null;
    }

    public hideTooltip() {
        this.timeout('hide_tooltip', () => this.model.show = false);
    }

}
