
import { Component, OnInit } from '@angular/core';
import { OverlayContentComponent } from '@acaprojects/ngx-widgets';

import { IRoom } from '../../../../services/data/rooms.service';

@Component({
    selector: 'equipment-details-modal',
    templateUrl: './equipment-details.component.html',
    styleUrls: ['./equipment-details.component.scss']
})
export class BookingEquipmentDetailsModalComponent extends OverlayContentComponent {
    /** ID of the space to show details for */
    public active_space: string = 'default';

    /** Selected spaces for booking */
    public get spaces(): IRoom[] {
        return this.model.spaces;
    }

    public model: { [name: string]: any } = {};

    public init(): void {
        if (!this.model.expected_attendees) {
            this.model.expected_attendees = {};
        }
        if (!this.model.notes) {
            this.model.notes = {};
        }
        if (!this.model.cost_code) {
            this.model.cost_code = {};
        }
        if (this.model.spaces && this.model.spaces.length > 0) {
            this.active_space = this.model.spaces[0].id;
        }
    }
}

