import { OverlayContentComponent } from '@acaprojects/ngx-widgets';
import { Component, ViewChild, ElementRef } from '@angular/core';
import { Utils } from '../../shared/utility.class';

@Component({
    selector: 'timezone-search-modal',
    templateUrl: './timezone-search-modal.template.html',
    styleUrls: ['./timezone-search-modal.styles.scss']
})
export class TimezoneSearchModalComponent extends OverlayContentComponent {

    @ViewChild('searchField') private field: ElementRef;

    public init() {
        if (!this.field) {
            return this.timeout('init', () => this.init());
        }
        this.field.nativeElement.focus();
        this.search();
    }

    public search() {
        this.model.filtered_timezones = [];
        this.model.loading = true;
        this.model.filtered_timezones = Utils.filter(this.model.search, Utils.getTimezones(), ['name']);
        for (const timezone of this.model.filtered_timezones) {
            if (timezone.match_name) {
                // Format match name
                timezone.match_name = timezone.match_name.replace(/\`[a-zA-Z0-9\@\.\_]*\`/g, '<span class="highlight">$&</span>');
                timezone.match_name = timezone.match_name.replace(/\`/g, '');
            }
        }
        this.model.loading = false;
    }

    public select(timezone: any) {
        this.model.selected = timezone;
        this.fn.event('Accept');
    }
}
