
import { trigger, state, style, animate, transition } from '@angular/animations';
import { OverlayContentComponent } from '@acaprojects/ngx-widgets';
import { Component } from '@angular/core';

import * as moment from 'moment';
import { Utils } from '../../shared/utility.class';

@Component({
    selector: 'conferencing-modal',
    templateUrl: './conferencing-modal.template.html',
    styleUrls: ['./conferencing-modal.styles.scss'],
    animations: [
        trigger('show', [
            transition(':enter', [style({ opacity: 0, height: 0 }), animate(300, style({ opacity: 1, height: '*' }))]),
            transition(':leave', [style({ opacity: 1, height: '*' }), animate(300, style({ opacity: 0, height: 0 }))]),
        ])
    ]
})
export class ConferencingModalComponent extends OverlayContentComponent {
    public model: any = {};

    public init() {
        this.model.countries = [
            { id: 'AU', name: 'Australia' },
            { id: 'UK', name: 'United Kingdom' },
            { id: 'US', name: 'USA' },
            { id: 'US', name: 'USA' },
            { id: 'US', name: 'USA' },
            { id: 'US', name: 'USA' },
        ];
        if (!this.model.conference) { this.model.conference = {}; }
    }

    public updateList(item) {
        let list = this.model.conference.list;
        if (!list) { list = []; }
        list.push(item);
        list = Utils.unique(list, 'id');
        this.model.conference.list = list;
    }

    public remove(item) {
        let list = this.model.conference.list;
        if (!list) { list = []; }
        const exists = list.find(i => i.id === item.id);
        if (exists) {
            list.splice(this.model.list.indexOf(exists), 1);
        }
        this.model.conference.list = list;
    }

    public setFirst(item) {
        this.model.conference.list.sort((a, b) => item.id === a.id ? -1 : 0);
    }
}
