


import { Injectable } from '@angular/core';
import { CommsService } from '@acaprojects/ngx-composer';

import { BaseService } from './base.service';

export interface ICateringMenuCategory {
    /** Database ID of the category */
    id: string;
    /** Display name for the category */
    name: string;
    /** List of zones that the category associates with */
    zones: string[];
    /** List of available items in the category */
    items: (ICateringMenuItem | ICateringMenuCategory)[];
    /** Whether this category identifed a package(category group) */
    package: boolean;
    /** URL to a image for the category */
    src: string;
    /** Number of this item package in the order */
    amount?: number;
    /** Number of items to select from package options */
    must_select?: number;
}

export interface ICateringMenuItem {
    /** Database ID for the item */
    id: string;
    /** Display name of the item */
    name: string;
    /** Month of the year the item starts being available */
    from: number
    /** Month of the year the item ends being available */
    to: number
    /** Time in hours before the order that is needed to prepare the item */
    notice: number;
    /** Time in hours before the booking that this item can be purchased  */
    cut_off: number;
    /** Cost of item without decimals. Decimal currencies should be normalised to integers e.g. $123.45 -> 12345 */
    cost: number;
    /** Minimum number of items allowed in an order */
    minimum: number;
    /** Maximum number of items allowed in an order */
    maximum: number;
    /** Allegen details for the item */
    allegy: string;
    /** List of category IDs associated with the item */
    categories: string[];
    /** Number of this item in the order */
    amount?: number;
    /** Whether the item is selected for the order */
    selected?: boolean;
}


@Injectable({
    providedIn: 'root'
})
export class CateringMenuService extends BaseService<ICateringMenuCategory> {

    constructor(protected http: CommsService) {
        super();
        this.model.name = 'catering-menu';
        this.model.route = '/menu';
    }

    /**
     * Convert user data to local format
     * @param user User data
     */
    protected processItem(item: { [name: string]: any }) {
        const category: ICateringMenuCategory = {
            id: item.id,
            name: item.name,
            zones: item.zones,
            items: this.process(item.items),
            package: item.package === 'true' || item.package === true,
            src: item.image_path
        }
        return category;
    }

    private process(list: (any)[]) {
        return (list || []).map(i => ({
            ...i,
            items: i.items ? this.process(i.items) : null,
            from: i.available_from,
            to: i.available_to,
            maximum: i.maximum_quantity,
            minimum: i.minimum_quantity,
            notice: i.notify_host,
            package: i.package === 'true' || i.package === true,
            src: i.image_path
        }))
    }

}
