import { OverlayContentComponent } from '@acaprojects/ngx-widgets';
import { Component } from '@angular/core';

import * as moment from 'moment';

import { RECURRENCE_PERIODS } from '../../shared/utilities/formatting.utilities';

@Component({
    selector: 'recurrence-modal',
    templateUrl: './recurrence-modal.template.html',
    styleUrls: ['./recurrence-modal.styles.scss'],
})
export class RecurrenceModalComponent extends OverlayContentComponent {
    public model: any = {
        periods: RECURRENCE_PERIODS,
        error: {}
    };

    public init() {
        if (!this.model.form) { this.model.form = { recurr_period: 0 }; }
        if (this.model.form.period) { this.model.form.recurr_period = this.model.form.period || 0; }
        if (this.model.form.end) { this.model.form.recurr_end = this.model.form.end || 0; }
        this.updateDisplay();
    }
    public selectDate(name: string = 'recurr_end') {
        this.service.Overlay.openModal('calendar', { data: { date: this.model.form[name], limit: 60 } }, (event) => {
            if (event.type === 'Accept') {
                if (event.data.form && event.data.form.date) {
                    const new_date = moment(event.data.form.date).endOf('d');
                    this.model.form[name] = new_date.valueOf();
                }
                this.updateDisplay();
            }
            event.close();
        });
    }

    public updateDisplay() {
        if (!this.model.form) { this.model.form = {}; }
        if (this.model.form.recurr_end) {
            const now = moment(this.model.date || undefined);
            const end = moment(this.model.form.recurr_end || undefined);
            this.model.recurr_end = now.isBefore(end) ? end.format('ddd, Do MMMM, YYYY') : now.format('ddd, Do MMMM, YYYY');
        } else {
            this.model.recurr_end = '';
        }
    }

    public clear() {
        this.model.form.recurr_end = null;
        this.updateDisplay();
    }

    public checkError() {
        let error = false;
        this.model.messages = [];
        if (this.model.form.recurr_period && !this.model.form.recurr_end) {
            this.model.messages.push('End date is required');
            this.model.error.recurr_end = true;
            error = true
        }
        return error;
    }

    public save() {
        if (!this.checkError()) {
            this.model.error = {};
            this.model.messages = [];
            this.event('Accept');
        }
    }
}
