
import { Component } from '@angular/core';
import { OverlayContentComponent } from '@acaprojects/ngx-widgets';

@Component({
    selector: 'item-pick-model',
    templateUrl: './item-pick-modal.template.html',
    styleUrls: ['./item-pick-modal.styles.scss']
})
export class ItemPickModalComponent extends OverlayContentComponent {

    public init() {
        if (!this.model.options) {
            this.model.options = [];
        }
    }

    public select() {
        const items: any[] = [];
        for (const item of this.model.options) {
            if (item.active) {
                items.push(item);
            }
        }
        this.model.selected = items;
        this.fn.event('Select');
    }

    public toggle(item) {
        item.active = !item.active;
    }

}
