import { OverlayContentComponent } from '@acaprojects/ngx-widgets';
import { Component, OnInit } from '@angular/core';

import * as moment from 'moment';
import { IBuilding, ILevel } from '../../services/data/buildings.service';

@Component({
    selector: 'room-filter-modal',
    templateUrl: './room-filter-modal.template.html',
    styleUrls: ['./room-filter-modal.styles.scss'],
})
export class RoomFilterModalComponent extends OverlayContentComponent {
    public model: any = {};
    /** Initial list of levels for the dropdown */
    public default_level_list: ILevel[];

    public init() {
        this.model.display = {};
        this.default_level_list = [{ id: -1, name: 'All Levels' }, ...this.service.Buildings.current().levels];
        this.updateDisplay();
    }

    public selectLocations() {
        const buildings = this.service.Buildings.list() || [];
        const locations = this.model.locations || [];
        for (const bld of buildings) {
            (bld as any).active = locations.indexOf(bld.id) >= 0
        }
        this.service.Overlay.openModal('pick-items', { data: {
            name: 'Locations',
            options: buildings && buildings.length > 0 ?  [...buildings] : []
        } }, (e) => {
            if (e.type === 'Select') {
                const new_locations = [];
                for (const bld of (e.data.selected || [])) {
                    if (bld.active) { new_locations.push(bld.id); }
                }
                this.model.locations = new_locations || [];
                this.updateDisplay();
                this.event('Update');
            }
            e.close();
        });
    }

    public event(name: string) {
        setTimeout(() => this.fn.event(name), 300);
    }

    public close() {
        setTimeout(() => this.fn.close(), 300);
    }

    public updateDisplay() {
        if (this.model.locations && this.model.locations.length > 0) {
            const l = this.model.locations;
            const blds = this.service.Buildings.list();
            const levels = [{ id: -1, name: 'All Levels' }];
            const list = l.length === 1
                ? (blds.find(i => i.id === l[0]) || { name: l[0] }).name
                : l.reduce((a, v) => {
                        const bld = blds.find(i => i.id === v) || { name: v };
                        if (bld.levels) { bld.levels.forEach(lvl => levels.push(lvl)); }
                        const b = bld.name;
                        return a ? a + ', ' + b : a + b;
                    }, '');
            if (l.length === 1) {
                const bld = blds.find(i => i.id === l[0]);
                if (bld.levels) { bld.levels.forEach(lvl => levels.push(lvl)); }
            }
            const active_level = this.model.level.list[this.model.level.index];
            this.model.level.list = levels;
            this.model.level.names = levels.map(i => i.name);
            this.model.level.index = Math.min(0, levels.findIndex(i => i.id === active_level.id));
            this.model.display.locations = `${l.length} Location${l.length === 1 ? '' : 's'}; ${list}`;
        } else {
            const active_level = this.model.level.list[this.model.level.index];
            this.model.level.list = this.default_level_list;
            this.model.level.names = this.default_level_list.map(i => i.name);
            this.model.level.index = Math.min(0, this.default_level_list.findIndex(i => i.id === active_level.id));
            this.model.display.locations = `Any location`;
        }
    }
}
