import { OverlayContentComponent } from '@acaprojects/ngx-widgets';
import { Component, ChangeDetectorRef } from '@angular/core';

import * as moment from 'moment';
import { AppService } from '../../services/app.service';

@Component({
    selector: 'time-extension',
    templateUrl: './time-extension.template.html',
    styleUrls: ['./time-extension.styles.scss'],
})
export class TimeExtensionModalComponent extends OverlayContentComponent {
    public model: any = {};
    /** Duration options that the user can select */
    public durations: number[] = [];
    /** Maximum amount of time that the booking can be extended */
    public max_extention = 60;
    /** Date to extend the meeting from */
    public extend_from: number;
    /** Whether the available durations are loading */
    public loading: boolean;

    public init() {
        this.loading = true;
        const from = moment(this.model.date).add(this.model.duration, 'm');
        let to = moment(from).add(this.max_extention, 'm');
        this.extend_from = from.valueOf();
        (this.service as AppService).Rooms.query({
            room_ids: this.model.room_list.join(','),
            available_from: Math.floor(this.extend_from / 1000),
            available_to: to.unix()
        }).then((room_list) => {
            this.durations = [];
            for (; to.isAfter(from, 'm'); to.subtract(15, 'm')) {
                let count = 0;
                const duration = to.diff(from, 'm');
                if (this.durations && this.durations.find(i => duration < i)) {
                    count = room_list.length;
                } else {
                    // Check if room is room is free between from and to
                    for (const room of room_list) {
                        for (const bkn of room.bookings) {
                            const start = moment(bkn.date);
                            const end = moment(start).add(bkn.duration, 'm');
                            if (from.isBetween(start, end, 'm', '[)')
                                || to.isBetween(start, end, 'm', '(]')
                                || start.isBetween(from, to, 'm', '()')) {
                                count--;
                                break;
                            }
                        }
                        count++;
                    }
                }
                if (count >= room_list.length) {
                    this.durations.push(duration);
                }
            }
            this.durations.sort((a, b) => a - b);
            this.loading = false;
        });
    }

    public select(time: any) {
        let eventDetails = { value: time, kind: 'time_selected' };
        this.fn.event(eventDetails);
    }

    public extend(duration: number) {
        this.model.extend_by = duration;
        setTimeout(() => this.fn.event('Extend'), 300);
    }
}
