import { MOCK_REQ_HANDLER } from '@acaprojects/ngx-composer';

import { BaseMockBackend } from './base.mock';

import * as moment from 'moment';
import { eventNames } from 'cluster';

export class MockRoomsBackend extends BaseMockBackend {
    protected load() {
        if (!this.model) { this.model = {}; }
        this.loadRooms();
    }

    private loadRooms() {
        if (this.model.log) { this.model.log('MOCK', 'Loading request for room list'); }
        // types[<room name>][<level zone>] = Array of room ids
        const types = {
            'Collab Room' : {
                'zone_lvl-L4': ['4.34', '4.36', '4.36', '4.27', '4.26'],
                'zone_lvl-L5': ['5.35', '5.36', '5.36', '5.27', '5.26'],
                'zone_lvl-L6': ['6.31', '6.32', '6.47', '6.48'],
                'zone_lvl-L7': ['7.31', '7.32', '7.47', '7.48'],
                'zone_lvl-L8': ['8.31', '8.32', '8.48', '8.48'],
                'zone_lvl-N19': ['19.31', '19.32', '19.41', '19.48'],
                'zone_lvl-N60': ['60.31', '60.32', '60.41', '60.48'],
                'zone_lvl-N61': ['61.31', '61.32', '61.41', '61.48'],
                'zone_lvl-N62': ['62.31', '62.32', '62.41', '62.48'],
                'zone_lvl-N63': ['63.31', '63.32', '63.41', '63.48'],
                'zone_lvl-N64': ['64.31', '64.32', '64.41', '64.48']

            },
            'Meeting Room' : {
                'zone_lvl-L5': ['5.37', '5.25']
            },
            'Partner Studio': {
                'zone_lvl-L5': ['5.24', '5.23', '5.22', '5.20', '5.13', '5.14', '5.15', '5.16', '5.17', '5.18'],
                'zone_lvl-L6': [
                    '6.03', '6.04', '6.06', '6.07', '6.08', '6.11', '6.12', '6.13', '6.14', '6.14', '6.15', '6.17', '6.18',
                    '6.21', '6.22', '6.23', '6.24', '6.25', '6.26', '6.27', '6.28', '6.29', '6.30', '6.33', '6.34', '6.36',
                    '6.37', '6.38', '6.39', '6.42', '6.43', '6.44', '6.45', '6.49', '6.50', '6.51', '6.52', '6.53', '6.54'
                ],
                'zone_lvl-L7': [
                    '7.03', '7.04', '7.06', '7.07', '7.08', '7.12', '7.13', '7.14', '7.14', '7.15', '7.17', '7.18',
                    '7.21', '7.22', '7.23', '7.24', '7.25', '7.26', '7.27', '7.28', '7.29', '7.30', '7.33', '7.34', '7.36',
                    '7.37', '7.38', '7.39', '7.42', '7.43', '7.44', '7.45', '7.49', '7.50', '7.51', '7.52', '7.53', '7.54'
                ],
                'zone_lvl-L8': [
                    '19.03', '19.04', '19.06', '19.08', '19.08', '19.12', '19.13', '19.14', '19.14', '19.15', '19.18', '19.18',
                    '19.21', '19.22', '19.23', '19.24', '19.25', '19.26', '19.28', '19.28', '19.29', '19.30', '19.33', '19.34', '19.36',
                    '19.38', '19.38', '19.39', '19.42', '19.43', '19.44', '19.45', '19.49', '19.50', '19.51', '19.52', '19.53', '19.54'
                ],
                'zone_lvl-N19': [
                    '60.03', '60.04', '60.06', '60.08', '60.08', '60.12', '60.13', '60.14', '60.14', '60.15', '60.18', '60.18' ],
                'zone_lvl-N60': [
                     '61.29', '61.30', '61.33', '61.34', '61.36',
                    '61.38', '61.38', '61.39', '61.42', '61.43', '61.44', '61.45', '61.49', '61.50', '61.51', '61.52', '61.53', '61.54'
                ],
                'zone_lvl-N61': [
                    '62.03', '62.04', '62.06', '62.08', '62.08', '62.12', '62.13', '62.14', '62.14', '62.15', '62.18', '62.18',
                    '62.21', '62.22', '62.23', '62.24' ]
                    ,
                'zone_lvl-N62': [
                    '63.03', '63.04', '63.06', '63.08', '63.08', '63.12', '63.13', '63.14', '63.14', '63.15', '63.18', '63.18',
                    '63.21', '63.22', '63.23', '63.24' ]
                    ,
                'zone_lvl-N63': [
                    '64.03', '64.04', '64.06', '64.08', '64.08', '64.12', '64.13', '64.14', '64.14', '64.15', '64.18', '64.18',
                    '64.21', '64.22', '64.23', '64.24' ]
            },
            'PD': {
                'zone_lvl-L5': ['5.48']
            },
            'Quiet Room': {
                'zone_lvl-L7': ['7.11'],
                'zone_lvl-L8': ['7.11']
            }
        };

        const capacity = {
            'Collab Room' : 12,
            'Meeting Room' : 16,
            'Partner Studio': 8,
            'PD': 12,
            'Quiet Room': 6,
            'Phone Booth': 2
        };

        const room_list: any[] = [];
        for (const name in types) {
            if (types.hasOwnProperty(name)) {
                for (const lvl in types[name]) {
                    if (types[name].hasOwnProperty(lvl)) {
                        for (const id of types[name][lvl]) {
                            room_list.push({ name, area: id, capacity: capacity[name] || 8, bookable: true, zone: lvl });
                        }
                    }
                }
            }
        }

        const bld = this.model.bld_data[Object.keys(this.model.bld_data)[0]];
        const extras = bld.settings.discovery_info.extras;
        const rooms: any[] = [];
        for (const room of room_list) {
            const id = `sys_rm-${room.area}`;
            let extra_list = '';
            for (let i = 0; i < 2; i++) {
                if (extra_list) { extra_list += ' '; }
                const e = extras[Math.floor(Math.random() * extras.length)];
                if (extra_list.indexOf(e.extra_id) < 0) { extra_list += e.extra_id; }
            }
            const bldn = this.model.backend.buildings.getBuilding(room.zone ||  'zone_lvl-L5');
            const zones = [room.zone || 'zone_lvl-L5'];
            if (bldn) { zones.push(bldn.id); }
            rooms.push({
                id,
                name: `${room.name} ${room.area}`,
                email: `${this.model.city}-${room.area}@${this.model.domain}`,
                bookable: room.bookable,
                capacity: room.capacity || Math.floor(Math.random() * 32 + 2),
                features: '',
                zones,
                settings: {
                    map_id: `${room.area}`,
                    bookings: [],
                    room_id: id,
                    extra_features: extra_list,
                    cost_hour: room.cost || 0,
                    bookable_by_request: room.request
                },
                support_url: `http://aca.engin.run/demo/aca/control/` // `/meeting/#/?ctrl=${id}`
            });
        }
        this.model.rooms = rooms;

        MOCK_REQ_HANDLER.register(`/${this.model.api_route}/rooms/:id`, this.model.rooms, (event) => {
            if (event.params.id) {
                if (event.fragment.available_from) {
                    const start = moment(event.fragment.available_from * 1000);
                    const end = event.fragment.available_to ? moment(event.fragment.available_to * 1000) : moment(start).add(60, 'm');
                    const available = this.getAvailableRooms(event.data, start.valueOf(), moment.duration(end.diff(start)).asMinutes());
                    return available.find(rm => rm.id === event.params.id);
                } else {
                    return event.data.find(rm => rm.id === event.params.id);
                }
            }
            return null;
        });

            // Get bookable rooms from room list
        MOCK_REQ_HANDLER.register(`/${this.model.api_route}/rooms`, this.model.rooms, (event) => {
            let data = event.data;
            if (event.fragment.zone_id) {
                data = data.filter(rm => rm.zones.indexOf(event.fragment.zone_id) >= 0);
            }
            if (event.fragment.available_from) {
                const start = moment(event.fragment.available_from * 1000);
                const end = event.fragment.available_to ? moment(event.fragment.available_to * 1000) : moment(start).add(60, 'm');
                const list = this.getAvailableRooms(data, start.valueOf(), moment.duration(end.diff(start)).asMinutes());
                return event.fragment.room_ids ? list.filter(i => event.fragment.room_ids.indexOf(i.id) >= 0) : list;
            } else {
                const results: any[] = [];
                for (const i of data) {
                    i.available = i.bookable && Math.floor(Math.random() * 2349873421) % 3 === 0;
                    results.push(i);
                }
                return results;
            }
        });
        this.state.next(true);
    }

    private getAvailableRooms(list: any[], start_time: number, duration: number = 60) {
        const results = [];
        const start = moment(start_time);
        const end = moment(start).add(duration, 'm');
        for (const rm of list) {
            let clash = false;
            for (const bkn of rm.settings.bookings) {
                const bkn_start = moment(bkn.Start);
                const bkn_end = moment(bkn.End);
                if (start.isBetween(bkn_start, bkn_end, 'm', '[)') || end.isBetween(bkn_start, bkn_end, 'm', '(]')) {
                    clash = true;
                }
            }
            if (!clash) {
                rm.available = true;
                rm.settings.available = true;
                results.push(rm);
            }
        }
        return results;
    }
}
