import { Component } from '@angular/core';
import { OverlayContentComponent } from '@acaprojects/ngx-widgets';

import { IBooking } from '../../services/data/bookings.service';

import * as moment from 'moment';

@Component({
    selector: 'confirm-request-modal',
    templateUrl: './confirm-request-modal.component.html',
    styleUrls: ['./confirm-request-modal.component.scss']
})
export class ConfirmRequestModalComponent extends OverlayContentComponent {
    /** Booking to confirm request */
    public booking: IBooking;

    public init() {
        const start = moment(this.model.booking.date);
        const end = moment(start).add(this.model.booking.duration, 'm');
        this.booking = {
            id: null,
            title: this.model.booking.title,
            room: this.model.booking.room[0],
            date: this.model.booking.date,
            organiser: this.model.booking.host,
            duration: this.model.booking.duration,
            attendees: this.model.booking.attendees,
            conferencing: this.model.booking.conferencing,
            display: {
                date: start.format('DD/MM/YYYY'),
                long_date: start.format('DD MMMM YYYY'),
                time: `${start.format('h:mma')} - ${end.format('h:mma')}`,
            }
        } as any;
    }
}
