import { OverlayContentComponent } from '@acaprojects/ngx-widgets';
import { Component } from '@angular/core';

import * as moment from 'moment';

import { FormValidators } from '../../shared/components/booking-form/form.validators';
import { CUSTOM_FIELD_REGISTER } from '../../shared/globals/custom-field-register';

@Component({
    selector: 'time-period-modal',
    templateUrl: './time-period-modal.template.html',
    styleUrls: ['./time-period-modal.styles.scss'],
})
export class TimePeriodModalComponent extends OverlayContentComponent {
    public model: { [name: string]: any } = {};

    public init() {
        if (!this.service || !this.service.Settings.setup) {
            return setTimeout(() => this.init(), 300);
        }
        this.model.form = {};
        this.model.fields = [
            {
                'key': 'date',
                'label': 'Date',
                hide: true,
                'control_type': 'custom',
                value: this.model.date,
                validators: [FormValidators.date],
                cmp: CUSTOM_FIELD_REGISTER.date
            },
            {
                'key': 'start',
                'label': 'Time',
                'refs': [ 'date' ],
                'control_type': 'custom',
                value: this.model.start_time,
                cmp: CUSTOM_FIELD_REGISTER.time
            },
            {
                'key': 'duration',
                'label': 'Duration',
                'refs': [ 'date' ],
                'control_type': 'custom',
                value: this.model.duration,
                validators: [FormValidators.duration()],
                cmp: CUSTOM_FIELD_REGISTER.duration
            }
        ];
        this.updateForm(this.model);
    }

    public updateForm(form) {
        this.model.form.start = form.start || form.start_time;
        const parts = form.start.split(':');
        const end = moment().hours(+parts[0]).minutes(+parts[1]).add(form.duration, 'm');
        this.model.form.end = end.format('HH:mm');
    }
}
